<script>
import {mapState, mapActions} from 'vuex';
import html2canvas from "html2canvas";
import moment from 'moment';

export default {
  data() {
    return {
      dialog: false,
      tipo:null,
      date:moment(new Date()).format('LL')
    };
  },
  props:['distribucion','ventas'],
  created(){
    moment.locale("es")
  },
  methods: {
    ...mapActions('clienteNamespace',['getCliente']),
    ocultarComprobante() {
      this.dialog = false;
    },
    calcularTotal(){},
    ordenarLista: function ( order ) {
      if(this.ventas.length > 0) {
        this.ventas.sort( (a, b) => {
          if( order == 'Unidad' ){
            if( a.unidad > b.unidad ) { return 1 }
            if( a.unidad < b.unidad ) { return -1 }
          }else{
            if( a.articulo > b.articulo ) { return 1 }
            if( a.articulo < b.articulo ) { return -1 }
          }
          return 0;
          
        });
        
      }
      if(this.distribucion.length > 0)
        this.distribucion.sort( (a, b) => {
          if( order == 'Unidad' ){
            if( a.unidad > b.unidad ) { return 1 }
            if( a.unidad < b.unidad ) { return -1 }
          }else{
            if( a.articulo > b.articulo ) { return 1 }
            if( a.articulo < b.articulo ) { return -1 }
          }
          return 0;
        });   
    },
    toPrint(){
      this.$htmlToPaper('factura')
    }
  },
};
</script>
