<template>
  <v-container>
    <v-row dense>
      <v-col cols="12" xs="12" sm="12">
        <highcharts :options="chartOptions"></highcharts>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
/* eslint-disable no-debugger */
import { Chart } from "highcharts-vue";
import { mapState } from "vuex";

export default {
  name: "chart",
  data() {
    return {
      title: "Total de ventas",
      valores: [],
      meses: [],
      totales: [],
    };
  },
  components: {
    highcharts: Chart,
  },
  props: {
    speed: Array,
    modo: String,
  },
  crated() {
    this.listar();
  },
  methods: {
    listar() {
      let me = this;
      let header = { Token: this.token };
      let configuracion = { headers: header };
      axios
        .get("venta/grafico12meses", configuracion)
        .then(function(response) {
          me.valores = response.data;
          me.graficar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    graficar() {
      let me = this;
      let mesn = "";
      me.valores.map(function(x) {
        switch (parseInt(x._id.mes)) {
          case 1:
            mesn = "Enero";
            break;
          case 2:
            mesn = "Febrero";
            break;
          case 3:
            mesn = "Marzo";
            break;
          case 4:
            mesn = "Abril";
            break;
          case 5:
            mesn = "Mayo";
            break;
          case 6:
            mesn = "Junio";
            break;
          case 7:
            mesn = "Julio";
            break;
          case 8:
            mesn = "Agosto";
            break;
          case 9:
            mesn = "Setiembre";
            break;
          case 10:
            mesn = "Octubre";
            break;
          case 11:
            mesn = "Noviembre";
            break;
          case 12:
            mesn = "Diciembre";
            break;
          default:
            mesn = "error";
        }

        me.meses.push(mesn + "-" + x._id.year);
        me.totales.push(x.total);
      });
    }
  },
  computed: {
    ...mapState("usuarioNamespace", ["token"]),
    chartOptions() {
      return {
        chart: { type: this.modo },
        title: { text: this.title },
        xAxis: {
          categories: [
            "Enero",
            "Febrero",
            "Marzo",
            "Abril",
            "Mayo",
            "Junio",
            "Julio",
            "Agosto",
            "Septiembre",
            "Octubre",
            "Noviembre",
            "Diciembre",
          ],
          crosshair: true,
        },
        yAxis: {
          min: 0,
          title: {
            text: "Rainfall (mm)",
          },
        },
        tooltip: {
          headerFormat:
            '<span style="font-size:10px">{point.key}</span><table>',
          pointFormat:
            '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
            '<td style="padding:0"><b>{point.y:.1f}</b></td></tr>',
          footerFormat: "</table>",
          shared: true,
          useHTML: true,
        },
        plotOptions: {
          column: {
            pointPadding: 0.2,
            borderWidth: 0,
          },
        },
        series: [
          {
            name: "Almacén",
            data: [
              48.9,
              38.8,
              39.3,
              41.4,
              47.0,
              48.3,
              59.0,
              59.6,
              52.4,
              65.2,
              59.3,
              51.2,
            ],
          },
          {
            name: "Reparto",
            data: [
              42.4,
              33.2,
              34.5,
              39.7,
              52.6,
              75.5,
              57.4,
              60.4,
              47.6,
              39.1,
              46.8,
              51.1,
            ],
          },
        ],
      };
    },
  },
};
/* eslint-disable no-debugger */
</script>
