<template>
  <v-container>
    <Dialog
      @save="guardarDeuda"
      @close="close"
      :object="objEdit"
      :show="abrirDialog"
    />
    <Indicadores :items="items" />
    <Chart :modo="modoChart" />
    <template>
      <span class="align-center">
        Clientes con deudas
      </span>
    </template>
    <v-row>
      <v-col>
        <v-text-field
          class="text-xs-center"
          v-model="search1"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
      </v-col>
    </v-row>
    <Table
      @edit="handleClick"
      :headers="headers"
      :arrayList="clientes | conDeuda"
      :search="search1"
      :opciones="true"
    />
    <template>
      <span class="align-center">
        Transacciones con deudas
      </span>
    </template>
    <v-row>
      <v-col>
        <v-text-field
          class="text-xs-center"
          v-model="search2"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
      </v-col>
      <v-col>
        <v-autocomplete
          v-model="value"
          :items="itemsFiltro"
          outlined
          dense
          label="Filtrar por clientes"
          @change="selectClient()"
        ></v-autocomplete>
      </v-col>
      
    </v-row>
    <Table
      @edit="handleClick"
      :headers="headersDeudas"
      :arrayList="arrayList"
      :search="search2"
      :select="value"
    />
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Indicadores from "../components/dashboard/Indicadores";
import Chart from "@/components/base/ChartVentas.vue";
import Table from "@/components/base/Table.vue";
import Dialog from "@/components/base/Dialog.vue";

export default {
  data() {
    return {
      search1: "",
      search2: "",
      itemsFiltro: [],
      values: [],
      value: null,
      modoChart: "column",
      abrirDialog: false,
      objEdit: {},
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Deuda", value: "deuda", sortable: true },
        { text: "Teléfono", value: "telefono", sortable: false },
        { text: "Email", value: "email", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      headersDeudas: [
        { text: "Persona", value: "persona.nombre", sortable: true },
        { text: "Venta", value: "venta", sortable: false },
        { text: "Total", value: "monto", sortable: false },
        { text: "Pagado", value: "pagado", sortable: false },
        { text: "Fecha", value: "createdAt", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      items: [
        {
          color: "warning",
          cuantity: "$ 2500",
          title: "Deudas",
          artist: "Foster the People",
        },
        {
          color: "success",
          cuantity: "$ 1200",
          title: "Balance",
          artist: "Ellie Goulding",
        },
      ],
      arrayList:[]
    };
  },
  components: {
    Indicadores,
    Chart,
    Table,
    Dialog,
  },
  async created() {
    this.llamarClientes();
    this.llamarVentas();
    this.clientesAutocomplete();
    this.getDeudas( this.token )
    this.arrayList = this.deudas

    // this.calcularDeuda();
  },
  filters: {
    conDeuda: (clientes) => clientes.filter((c) => c.deuda > 0),
  },
  computed: {
    ...mapState("clienteNamespace", ["clientes"]),
    ...mapState("ventasNamespace", ["ventas"]),
    ...mapState("deudasNamespace", ["deudas"]),
    ...mapState("usuarioNamespace", ["token"]),
  },
  methods: {
    ...mapActions("clienteNamespace", ["getClientes"]),
    ...mapActions("clienteNamespace", ["updateDeuda"]),
    ...mapActions("ventasNamespace", ["getVentas"]),
    ...mapActions("deudasNamespace", ["getDeudas"]),
    clientesAutocomplete() {
      this.itemsFiltro = this.clientes.map( i => {
        let obj = {
          text: i.nombre, 
          value: i._id
        }
        return obj
      });
    },
    llamarClientes() {
      let Token = this.token;
      this.getClientes(Token);
    },
    llamarVentas() {
      let Token = this.token;
      this.getVentas(Token);
    },
    handleClick(obj) {
      debugger;
      this.objEdit = obj;
      this.abrirDialog = true;
    },
    guardarDeuda(obj) {
      debugger;
      this.updateDeuda(obj);
      this.close();
      this.llamarClientes();
    },
    close() {
      this.abrirDialog = false;
    },
    selectClient(){
      
      this.arrayList=[]
      if( this.value != null && this.value != '' ){
        this.deudas.forEach(element => {
          if( this.value == element.persona._id ){
            this.arrayList.push( element )
          }
        });
      }
      if( this.arrayList.length == 0){ this.arrayList = this.deudas }
    }
  },
};
</script>
