import axios from "axios";
// import bus from "@/EventBus";

export default {
  namespaced: true,
  state: {
    cliente: {},
    clientes: []
  },
  mutations: {
    llenarCliente(state, clienteAccion) {
      state.cliente = clienteAccion;
    },
    llenarClientes(state, data) {
      state.clientes = data;
      // bus.$emit('cargarLista');
    },
  },
  actions: {
    getCliente: async function({ commit }, data_persona) {
      let persona = data_persona.persona
      let token = data_persona.token
      let header = { Token: token };
      let configuracion = { headers: header };
      let data = null;
      
      await axios
        .get(`persona/query?_id=${persona._id}`,configuracion)
        .then(function(response) {
          data = response.data;
          commit("llenarCliente", data);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getClientes: async function({ commit }, token) {
      let header = { Token: token };
      let configuracion = { headers: header };
      let data = null;
      
      await axios
        .get('persona/listClientes',configuracion)
        .then(function(response) {
          data = response.data;
          commit("llenarClientes", data);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    updateCliente: async function({ dispatch }, data_persona) {
      let header = { Token: data_persona.token };
      let configuracion = { headers: header };
      await axios
        .put('persona/update',{
          _id: data_persona.id,
          tipo_persona: data_persona.tipo_persona,
          nombre: data_persona.nombre,
          tipo_documento: data_persona.tipo_documento,
          num_documento: data_persona.num_documento,
          direccion: data_persona.direccion,
          telefono: data_persona.telefono,
          email: data_persona.email,
        },configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `El cliente ${res.data.nombre} ${res.data.apellido} fue actualizado correctamente`,
            icon: "success",
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    updateDeuda: async function({ dispatch }, data_persona) {
      let header = { Token: data_persona.token };
      let configuracion = { headers: header };
      await axios
        .put('persona/updateDeuda',{
          _id: data_persona.id,
          deuda: data_persona.deuda
        },configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `La deuda de ${res.data.nombre} ${res.data.apellido} fue actualizada correctamente`,
            icon: "success",
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    saveCliente: async function({dispatch}, data_persona) {
      let header = { Token: data_persona.token };
      let configuracion = { headers: header };
      await axios
        .post('persona/add',{
          tipo_persona: data_persona.tipo_persona,
          nombre: data_persona.nombre,
          tipo_documento: data_persona.tipo_documento,
          num_documento: data_persona.num_documento,
          direccion: data_persona.direccion,
          telefono: data_persona.telefono,
          email: data_persona.email,
        },configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `El cliente ${res.data.nombre} ${res.data.apellido} fue agregado exitosamente`,
            icon: "success",
          }); 
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activateCliente: async function({ dispatch }, data_persona) {
      let header = { Token: data_persona.token };
      let configuracion = { headers: header };
      await axios
        .put('persona/activate',{_id: data_persona.id,},configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `El cliente ${res.data.nombre} ${res.data.apellido} fue activado correctamente`,
            icon: "success",
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    deactivateCliente: async function({ dispatch }, data_persona) {
      let header = { Token: data_persona.token };
      let configuracion = { headers: header };
      await axios
        .put('persona/deactivate',{_id: data_persona.id,},configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `El cliente ${res.data.nombre} ${res.data.apellido} fue desactivado correctamente`,
            icon: "success",
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    deleteCliente: async function({ dispatch }, data_persona) {
      let token = data_persona.token
      let header = { Token: token };
      let configuracion = { headers: header };
      
      await axios
        .delete(`persona/remove?_id=${data_persona.id}`,configuracion)
        .then(function(res) {
          dispatch('getClientes', data_persona.token)
          swal({
            title: "Buen trabajo!",
            text: `El cliente ${res.data.nombre} ${res.data.apellido} fue eliminado correctamente`,
            icon: "success",
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  }
};
