import axios from "axios";
import moment from 'moment';
export default {
  namespaced: true,
  state: {
    deudas: null
  },
  mutations: {
    llenarDeudas(state, data) {
      state.deudas = data;
      // state.ventas.push(data);
    },
  },
  actions: {
    getDeudas: async function({ commit }, token) {
      let header = { Token: token };
      let configuracion = { headers: header };
      let data = null;
      
      await axios
        .get('deuda/list',configuracion)
        .then(function(response) {
          response.data.forEach( (element) => {
            element.createdAt = moment(element.createdAt).format("LL");
          })
          data = response.data;
          commit("llenarDeudas", data);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  }
};
