<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <!--Data Table-->
        <v-data-table
          :headers="headers"
          :items="arrayList"
          :search="search"
          :single-select="true"
          class="elevation-1"
        >
          <template v-if="opciones" v-slot:item.opciones="{ item }">
            <v-icon 
            small 
            class="mr-2" 
            @click="editItem(item)"
            >edit
            </v-icon>
          </template>
          <template v-slot:item.estado="{ item }">
            <div v-if="item.estado">
              <span class="blue--text">Activo</span>
            </div>
            <div v-else>
              <span class="red--text">Inactivo</span>
            </div>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import {mapState} from 'vuex';
export default {
  name: "Table",
  data() {
    return {
      itemObj: {
        id: "",
        nombre: "",
        deuda: "",
        token: "",
      },
      personaObj: {
        id:'',
        deuda:'',
        token:''
      },
    };
  },
  props: {
    headers: Array,
    arrayList: Array,
    search: "",
    select: "",
    opciones: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState('usuarioNamespace',['token'])
  },
  methods: {
    editItem(item) {
      debugger;
      this.itemObj.id = item._id;
      this.itemObj.nombre = item.nombre;
      this.itemObj.deuda = item.deuda;
      this.itemObj.token = this.token;
      this.$emit("edit", this.itemObj);
    },
  },
};
</script>
