<template>
  <v-container>
    <v-row wrap>
      <Indicadores1 :items="items" />
    </v-row>
    <v-row> </v-row>
    <v-row wrap>
      <v-col cols="112" xs="12" sm="12" md="6">
        <ChartClientes :modo="modoChartClientes" />
      </v-col>
      <v-col cols="112" xs="12" sm="12" md="6">
        <ChartVentas :modo="modoChartVentas" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import bus from "@/EventBus";
import { mapState, mapActions } from "vuex";
import Indicadores2 from "../components/dashboard/Indicadores2";
import Indicadores1 from "../components/dashboard/Indicadores";
import VentasReporte from "../components/ReporteVenta";
import ChartVentas from "../components/base/ChartVentas";
import ChartClientes from "../components/base/ChartClientes";
export default {
  data() {
    return {
      modoChartVentas: "column",
      modoChartClientes: "pie",
      ventasA: [],
      ventasB: [],
      pedidosA: [],
      items: [
        {
          color: "success",
          cuantity: "",
          title: "Pedidos",
          artist: "Pedidos actuales",
        },
        {
          color: "success",
          cuantity: "",
          title: "Clientes",
          artist: "Total de clientes",
        },
        {
          color: "success",
          cuantity: "",
          title: "Ventas",
          artist: "Ventas Almacén",
        },

        {
          color: "success",
          cuantity: "",
          title: "Ventas",
          artist: "Ventas DIstribución",
        },
      ],
    };
  },
  created() {
    this.diferenciarVentas();
    this.totalClientes();
    this.totalVentasAlmacen();
    this.totalVentasDistribucion();
    this.totalPedidos();
    this.getClientes(this.token).then();
    this.getVentas(this.token).then();
    this.getDeudas(this.token).then();
    this.getUsuarios(this.token).then();
  },
  components: {
    Indicadores1,
    Indicadores2,
    VentasReporte,
    ChartVentas,
    ChartClientes,
  },
  computed: {
    ...mapState("usuarioNamespace", ["token", "usuario"]),
    ...mapState("clienteNamespace", ["clientes"]),
    ...mapState("ventasNamespace", ["ventas"]),
  },
  methods: {
    ...mapActions("clienteNamespace", ["getClientes"]),
    ...mapActions("ventasNamespace", ["getVentas"]),
    ...mapActions("deudasNamespace", ["getDeudas"]),
    ...mapActions("usuarioNamespace", ["getUsuarios"]),
    diferenciarVentas() {
      this.ventas.forEach((e) => {
        if (e.entrega != false) {
          e.negocio == 1 ? this.ventasA.push(e) : this.ventasB.push(e)
        } else {
          this.pedidosA.push(e);
        }
      });
    },
    totalPedidos() {
      this.items[0].cuantity = this.pedidosA.length;
    },
    totalClientes() {
      this.items[1].cuantity = this.clientes.length;
    },
    totalVentasAlmacen() {
      this.items[2].cuantity = this.ventasA.length;
    },
    totalVentasDistribucion() {
      this.items[3].cuantity = this.ventasB.length;
    },
  },
};
</script>
